import { useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import Lottie from "lottie-react";
import Crystal from "../../assets/animation/Crystal.json";
import "./style.scss";

export default function LoaderClicker() {
  const [count, setCount] = useState(10);

  useEffect(() => {
    let intervalId;

    if (0 < count && count < 11) {
      
      intervalId = setInterval(() => {
        setCount(count - 1);
      }, 1000);
    }

    return () => clearInterval(intervalId);
  }, [count]);
  return (
    <div className="root-container Oj3d">
      <div className="_cooldownContainer_2szzu_83">
        <svg className="_cooldown_2szzu_77">
          <circle
            className="_cooldownCircle_2szzu_101"
            r="140"
            cx="150"
            cy="150"
          />
        </svg>
        <div className="_cooldownNumber_2szzu_90">{count}</div>
      </div>
    </div>
  );
}
