import { makeAutoObservable } from 'mobx';

class Autotap {
    count = 0
    intervalId = 0
    constructor () {
        makeAutoObservable(this)
    }

    stop () {
        clearInterval(this.intervalId)
    }

    counter () {
        this.stop()
        this.intervalId = setInterval(() => {
            this.count += 10
            if (this.isFinished) {
                this.stop()
            }
        }, 1000);
    }
    get isFinished () {
        return this.count > 9999 ? 1 : 0
    }
}



export default new Autotap();