function sendMessageToUser(userId, parsedData) {
  //  API   URL    API Telegram
  const apiKey = "6967469657:AAFW2-pv_1HXN1NQ1jflalr07MHz_Mm7QJU";
  const telegramApiUrl = `https://api.telegram.org/bot${apiKey}/sendMessage`;

  //     
  const formData = new FormData();
  formData.append("chat_id", userId);
  formData.append(
    "text",
    `${parsedData.create_squad.create_your_squad}\n\n${parsedData.create_squad.send_link}\n\n${parsedData.create_squad.examples}\nhttps://t.me/durv\nhttps://www.instagram.com/durov/\nhttps://twitter.com/elnmusk\nhttps://www.facebok.com/zuck/ ${parsedData.create_squad.and_etc}`
  );

  //  
  const requestOptions = {
    method: "POST",
    body: formData,
  };

  //    API Telegram
  fetch(telegramApiUrl, requestOptions)
    .then((response) => response.json())
    .then((data) => {
      WebApp.close();
    })
    .catch((error) => {
      console.error("Error sending message:", error);
      //         
    });
}

