import "./style.scss";

export default function LeagueSkeleton() {
  return (
    <ul className="league-skeleton">
      {Array.from(Array(5), (e, i) => {
        return (
          <li key={i} className="league-skeleton__wrapper">
            <div className="league-skeleton__circle"></div>
            <div className="league-element__box1"></div>
            <div className="league-element__box2"></div>
            <div className="league-element__box3"></div>
          </li>
        );
      })}
    </ul>
  );
}
