import React from "react";
import CountUp from "react-countup";

export default function Counter({ start, end, onEnd }) {
  // const handleEnd = () => {
  //   //  updateOldScore     score
  //   updateOldScore(score);
    
  //   //  "sfsdf"  
  //   console.log("sfsdf");
    
  //   //   ,   ,     end
  //   if (onEnd) {
  //     onEnd(end);
  //   }
  // };

  return (
    <CountUp
      duration={1.25}
      start={start}
      end={end}
      delay={0}
      separator=","
      easingFn={(t, b, c, d) => (c * t / d) * (3 / (2 + t / d)) + b}
      // onEnd={handleEnd}
    />
  );
}
